body {
  background: #FFF;
  font-family: "Proxima Nova", "Helvetica", Arial, sans-serif;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  font-size: 14px;
  line-height: 129%;
  color: #1F1F1F;
}
body.no-scroll {
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  body {
    font-size: 16px;
    line-height: 140%;
  }
}
@media screen and (min-width: 1800px) {
  body {
    font-size: 18px;
    line-height: 144%;
  }
}
a {
  text-decoration: none;
  color: inherit;
}

button {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}

ul, ol {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

picture {
  display: block;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.background-image {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}
.background-image picture {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.background-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.swiper-button-prev, .swiper-button-next {
  width: 48px;
  height: 48px;
  padding: 6px;
  background: var(--boxBg);
  border-radius: 3px;
  pointer-events: auto;
  display: none;
  transition: var(--transition);
}
.swiper-button-prev::after, .swiper-button-next::after {
  color: #1F1F1F;
  font-size: 20px;
}
.swiper-button-prev:hover, .swiper-button-prev:focus, .swiper-button-next:hover, .swiper-button-next:focus {
  background: var(--accent);
}
.swiper-button-prev:hover::after, .swiper-button-prev:focus::after, .swiper-button-next:hover::after, .swiper-button-next:focus::after {
  color: #fff;
}
.swiper-button-prev:active, .swiper-button-next:active {
  background: var(--thColor);
}
.swiper-button-prev:active::after, .swiper-button-next:active::after {
  color: #fff;
}
.swiper-button-prev {
  left: 10px;
}
.swiper-button-next {
  right: 10px;
}

.range {
