.btn {
  padding: 9px 19px;
  font-weight: 600;
  font-size: 14px;
  line-height: 100%;
  text-align: center;
  color: #fff;
  background: var(--accent);
  border-radius: 3px;
  border: 1px solid transparent;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: var(--transition);
}
.btn:hover, .btn:focus {
  background: #B22414;
}
.btn:active {
  background: #942114;
}
.btn--sec {
  background: #EAEAEA;
  color: #1F1F1F;
}
.btn--sec:hover, .btn--sec:focus {
  background: #D8D8D8;
  color: #1F1F1F;
}
.btn--sec:active {
  background: var(--thColor);
  color: #fff;
}
.btn--third {
  background: var(--thColor);
  color: #fff;
}
.btn--third:hover, .btn--third:focus {
  background: #4B4B4B;
}
.btn--third:active {
  background: var(--thColor);
}
.btn--other {
  background: #fff;
  color: #1F1F1F;
  border: 1px solid var(--textSec);
}
.btn--other:hover, .btn--other:focus {
  background: #1F1F1F;
  border-color: #1F1F1F;
  color: #fff;
