  line-height: 100%;
  color: var(--textSec);
}
.form__field {
  width: 100%;
  height: 42px;
  padding: 11px 14px;
  border: 1px solid var(--textSec);
  border-radius: 3px;
  font-weight: 400;
  font-size: 14px;
  line-height: 100%;
}
.form__field::placeholder {
  color: var(--textSec);
}
.form__submit {
  margin-bottom: var(--formMargin);
}
.form__agree {
  margin-bottom: var(--formMargin);
  font-size: 11px;
  line-height: 130%;
}
.form__agree-label {
  display: flex;
  align-items: flex-start;
  cursor: pointer;
}
.form__agree-checkbox {
  width: 20px;
  min-width: 20px;
  height: 20px;
  margin-right: 14px;
  margin-top: 3px;
  display: inline-block;
  position: relative;
  z-index: 1;
}
.form__agree-checkbox::before {
  content: "";
  position: absolute;
  top: -8px;
  left: -8px;
  right: -8px;
  bottom: -8px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.05);
  opacity: 0;
  transition: var(--transition);
}
.form__agree-checkbox:hover::before, .form__agree-checkbox:focus::before {
  opacity: 1;
}
.form__agree-checkbox__check {
  width: 20px;
  min-width: 20px;
  height: 20px;
  display: inline-block;
  border: 1px solid #BEBEBE;
  border-radius: 4px;
  background: #fff;
  position: relative;
}
.form__agree-checkbox__check::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 15px;
  height: 10px;
  background: url("../images/icons/check.svg") no-repeat center/contain;
  opacity: 0;
}
.form__agree a {
  text-decoration: underline;
}
.form__agree input {
  display: none;
}
.form__agree input:checked ~ .form__agree-checkbox .form__agree-checkbox__check {
  background: var(--accent);
  border: 1px solid var(--accent);
}
.form__agree input:checked ~ .form__agree-checkbox .form__agree-checkbox__check::after {
  opacity: 1;
}
